  margin-top: 0.2rem;
}

.cb-pk-verify {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}

.cb-pk-verify-header {
  text-align: center;
}

.cb-pk-verify-icons-section {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  margin-top: 1.5rem;
}

.cb-pk-verify-icons-section-icon {
  height: 3.75rem;
}

.cb-pk-verify-body-section {
  display: flex;
  flex-direction: column;
  margin: 2.5rem auto 2rem;
  gap: 0.5rem;
  width: 60%;
  text-align: center;
  align-self: center;
}

.cb-pk-verify-spinner {
  border-top-color: var(--cb-primary-color);
  border-right-color: var(--cb-primary-color);
  width: 2rem;
  height: 2rem;
  margin: 1rem auto;
}

.cb-pk-verify-fallbacks {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  align-self: center;
  margin: 1.25rem auto 1rem;
}

/********** Dialog / Modal **********/
.cb-dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.cb-dialog-content {
  background: var(--cb-white);
  padding: 1.5rem;
  border-radius: var(--cb-border-radius-lg);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  width: 290px;
}

.cb-dialog-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0.6rem 0rem 1rem 0rem;
}

.cb-dialog-header-text {
  margin-top: 0rem;
}

.cb-dialog-body {
  margin-bottom: 2.5rem;
}

.cb-dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.cb-dialog-x-button {
  cursor: pointer;
  height: 1.5rem;
}

.cb-dialog-button {
  flex: 1;
  margin: auto;
  text-align: center;
}

/********** Passkey List **********/
.cb-passkey-list-card {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  border-radius: var(--cb-border-radius-lg);
  border: var(--cb-border-color) 1px solid;
  margin: 0.5rem 0;
  padding: 0.5rem;
  background-color: var(--cb-white);
}

.cb-passkey-list-icon {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.cb-passkey-list-icon img {
  width: 2rem;
  height: 2rem;
}

.cb-passkey-list-icon svg {
  width: 2rem;
  height: 2rem;
  fill: var(--cb-text-primary-color);
}

.cb-passkey-list-details {
  flex: 5;
  display: flex;
  flex-direction: column;
  justify-content: left;
  line-height: normal;
  gap: 0.5rem;
}

.cb-passkey-list-header {
  display: flex;
  flex-direction: row;
  justify-content: left;
  gap: 1rem;
}

.cb-passkey-list-header-title {
  margin: auto 0;
}

.cb-passkey-list-header-badge {
  border-radius: var(--cb-border-radius-lg);
  border: var(--cb-dark) 1px solid;
  padding: 0.5rem;
}

.cb-passkey-list-delete:hover {
  fill: var(--cb-error-text-color);
  cursor: pointer;
}

.cb-passkey-list-primary-button {
  margin: 1.25rem auto;
  display: flex;
  justify-content: center;
}

.cb-error-page {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 0.5rem auto 1.5rem auto;
}

.cb-error-page-body {
  text-align: center;
  margin: 2rem auto 2.25rem auto;
}

.cb-confirm-process-abort {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.cb-confirm-process-abort-container {
  padding: 0.85rem 2.3rem;
  border: 1px solid var(--cb-border-color);
  border-radius: var(--cb-border-radius-lg);
  margin-top: 0.7rem;
  margin-bottom: 2rem;
}

.cb-confirm-process-abort-icon {
  height: 1.9rem;
}

.cb-user-details-section {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  border: 1px solid var(--cb-border-color);
  border-radius: var(--cb-border-radius-lg);
  padding: 1rem 1.25rem;
}

.cb-user-details-section-header {
  margin-bottom: 1rem;
}

.cb-user-details-section-indentifier {
  width: 85%;
  align-self: self-start;
}

.cb-user-details-section-indentifiers-list {
  margin-top: 1rem;
}

.cb-user-details-section-indentifiers-list-item {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
}

.cb-user-details-section-indentifiers-list-item-field {
  flex: 8;
}

.cb-user-details-section-indentifiers-list-item-field-input {
  width: 100%;
}

.cb-user-details-section-indentifiers-list-item-badge {
  padding: 0.5rem;
  flex: 1;
  margin-top: 0.75rem;
  border-radius: var(--cb-border-radius-sm);
}

.cb-user-details-section-indentifiers-list-item-badge-primary {
  background-color: var(--cb-primary-color);
  color: var(--cb-button-text-primary-color);
}

.cb-user-details-section-indentifiers-list-item-badge-secondary {
  background-color: var(--cb-text-secondary-color);
  color: var(--cb-text-primary-color);
}

.cb-user-details-section-indentifiers-list-item-badge-text {
  color: var(--cb-button-text-primary-color);
}

.cb-social-login-divider {
  margin: 0 0 0.75rem 0;
}

.cb-social-login-buttons-section {
  display: flex;
  gap: 0.75rem;
  justify-content: space-around;
  width: 90%;
  align-self: center;
  margin-bottom: 1rem;
}

.cb-social-login-buttons-section-large {
  flex-direction: column;
}

.cb-social-login-buttons-section-small {
  flex-direction: row;
}

.cb-social-login-buttton-large img {
  height: 1.25rem;
  padding-right: 0.25rem;
}

.cb-social-login-button-loading-spinner {
  border-top-color: var(--cb-primary-color);
  border-right-color: var(--cb-primary-color);
  width: 0.9rem;
  height: 0.9rem;
}

.cb-last-identifier {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  border-radius: var(--cb-border-radius-sm);
  border: var(--cb-border-color) 1px solid;
  margin: 0.75rem 0;
  padding: 0.6rem 0.5rem 0.6rem 1rem;
  align-self: center;
  cursor: pointer;
  width: 83%;
}

.cb-last-identifier:hover {
  background-color: var(--cb-box-color-hover);
}

.cb-last-identifier-disabled {
  cursor: default;
  background-color: var(--cb-box-color-hover);
}

.cb-last-identifier-icon {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.cb-last-identifier-icon img {
  width: 2rem;
  height: 2rem;
}

.cb-last-identifier-details {
  flex: 5;
  display: flex;
  flex-direction: column;
  justify-content: left;
  line-height: normal;
  gap: 0.35rem;
}

.cb-last-identifier-spinner {
  border-top-color: var(--cb-text-primary-color);
  border-right-color: var(--cb-text-primary-color);
  border-left-color: var(--cb-text-primary-color);
  width: 1.5rem;
  height: 1.5rem;
  box-sizing: border-box;
}

.cb-last-identifier-skip-button {
  margin: 0.5rem auto 0.75rem auto;
  width: 90%;
}

@media screen and (max-width: 481px) {
  .cb-container {
    width: 360px;
  }

  .cb-user-details-section-indentifier {
    width: 75%;
  }
}
